html {
  overflow-x: hidden;
  overflow-y: hidden;
}

body {
  font-family: 'frt', sans-serif;
  direction: rtl;
}

* {
  font-family: inherit;
}

input, a, button {
  font-family: 'frt', sans-serif;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "frt";
  src: url("../fonts/FrutigerLTArabic-55Roman.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-55Roman.woff") format("woff"), url("../fonts/FrutigerLTArabic-55Roman.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-55Roman.eot") format("eot");
}

@font-face {
  font-family: "frtBold";
  src: url("../fonts/FrutigerLTArabic-65Bold.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-65Bold.woff") format("woff"), url("../fonts/FrutigerLTArabic-65Bold.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-65Bold.eot") format("eot");
}

@font-face {
  font-family: "frtLight";
  src: url("../fonts/FrutigerLTArabic-45Light.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-45Light.woff") format("woff"), url("../fonts/FrutigerLTArabic-45Light.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-45Light.eot") format("eot");
}

@font-face {
  font-family: "frtBlk";
  src: url("../fonts/FrutigerLTArabic-75Black.ttf") format("ttf"), url("../fonts/FrutigerLTArabic-75Black.woff") format("woff"), url("../fonts/FrutigerLTArabic-75Black.woff2") format("woff2"), url("../fonts/FrutigerLTArabic-75Black.eot") format("eot");
}

.flix, .preloader, .head .up, .navBar, .navBar > ul, .product .pic, .product .pic .top, .boxes .box, .boxes .box .pic {
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes shape {
  from {
    opacity: 0;
    top: -103%;
    left: -100%;
  }
  to {
    top: 50%;
    left: 65%;
  }
}

.noBg {
  background-color: transparent !important;
}

.preloader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  flex-direction: column;
  z-index: 9999;
}

.menuTriger {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(193, 150, 77, 0.8);
  position: fixed;
  top: 25px;
  right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 99;
  display: none;
}

@media (max-width: 991px) {
  .menuTriger {
    display: flex;
  }
}

.menuTriger i {
  color: #ffffff;
  font-size: 20px;
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(91, 91, 91, 0.95);
  z-index: 9;
  transition: .5s;
  opacity: 0;
  visibility: hidden;
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.sideNav {
  position: fixed;
  top: 0;
  right: -261px;
  width: 260px;
  height: 100%;
  background-color: #FFF;
  z-index: 99;
  overflow-y: auto;
  transition: .5s;
}

.sideNav.open {
  right: 0;
}

.sideNav .options {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #888;
}

.sideNav .options .close1 {
  display: flex;
  font-size: 20px;
  color: #dedede;
  cursor: pointer;
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 260px;
  padding: 0px 25px 0;
}

.sideNav > ul > li {
  position: relative;
  padding: 7px 15px;
  width: 100%;
}

.sideNav > ul > li:not(:last-child) {
  border-bottom: 1px solid #dedede;
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-size: 16px;
  color: #c1964d;
  transition: .5s;
}

.sideNav > ul > li a:hover {
  color: #5b5b5b;
}

.sideNav > ul > li i {
  position: absolute;
  top: 13px;
  left: 13px;
  transform: rotate(90deg);
  font-size: 9px;
  color: currentColor;
  transition: .5s;
}

.sideNav > ul > li i.open {
  transform: rotate(0deg);
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: .5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

.head {
  background-image: url("../images/logoBack.png");
  background-position: center;
  background-repeat: no-repeat;
  background-position-y: top;
}

.head .container {
  position: relative;
}

.head .up {
  position: absolute;
  top: 0;
  left: 0;
  width: max-content;
  padding-top: 15px;
  padding-left: 15px;
}

.head .up .lang {
  font-size: 12px;
  color: #b0b0b0;
  margin-right: 20px;
  transition: .5s;
}

.head .up .lang:hover {
  color: #c1964d;
}

.head .up .social a {
  color: #c3c3c3;
  font-size: 16px;
  margin-left: 22px;
  transition: .5s;
}

.head .up .social a:hover {
  color: #c1964d;
}

.head .up .social a:last-child {
  margin-left: 10px;
}

@media (max-width: 991px) {
  .head .up {
    display: none;
  }
}

.head .content {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 991px) {
  .head .content {
    flex-direction: column;
  }
}

.head .search {
  position: relative;
  width: max-content;
  margin-top: 25px;
  width: max-content;
}

.head .search.open {
  opacity: 1;
  visibility: visible;
  top: 60%;
}

.head .search.open .bar {
  cursor: pointer;
}

.head .search.open .bar span {
  opacity: 1;
}

@media (max-width: 991px) {
  .head .search {
    order: 3;
    position: absolute;
    top: 100%;
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
  }
}

@media (max-width: 767px) {
  .head .search {
    margin-top: 10px;
  }
}

.head .search .bar {
  display: none;
  width: 40px;
  height: 40px;
  position: relative;
}

.head .search .bar span {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #b0b0b0;
  position: absolute;
  transition: .3s;
}

.head .search .bar span:first-child {
  transform-origin: left;
  transform: rotate(47deg);
  top: 25%;
  left: 16.5px;
}

.head .search .bar span:last-child {
  transform-origin: right;
  transform: rotate(-47deg);
  top: 25%;
  right: 10px;
}

@media (max-width: 991px) {
  .head .search .bar {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -85px;
  }
}

.head .search input {
  width: 260px;
  height: 55px;
  border: 0;
  border-bottom: 1px solid #e2e2e2;
  padding: 0 10px;
  color: #b0b0b0;
  transition: .5s;
}

.head .search input::placeholder {
  color: #b0b0b0;
}

.head .search input:focus {
  border-bottom: 1px solid #c1964d;
}

@media (max-width: 991px) {
  .head .search input {
    width: 600px;
    background-color: transparent;
    border-bottom: 1px solid #e2e2e2;
    color: #b0b0b0;
  }
  .head .search input::placeholder {
    color: #b0b0b0;
  }
  .head .search input:focus {
    border-bottom: 1px solid #5b5b5b;
  }
}

@media (max-width: 767px) {
  .head .search input {
    width: 400px;
  }
}

@media (max-width: 480px) {
  .head .search input {
    width: 300px;
  }
}

.head .search button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 30px;
  border-radius: 50%;
  background-color: transparent;
  border: 0;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .5s;
}

@media (min-width: 992px) {
  .head .search button {
    left: 10px;
  }
}

@media (min-width: 1200px) {
  .head .search button {
    left: 10px;
  }
}

@media (max-width: 991px) {
  .head .search button {
    left: 10px;
  }
}

.head .search button span {
  transition: .5s;
  color: #c1964d;
}

.head .search button:hover span {
  color: #c3c3c3;
}

.head .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 9;
  display: none;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  z-index: 99;
}

.head .menu {
  margin-top: 25px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 260px;
}

@media (max-width: 991px) {
  .head .menu {
    justify-content: center;
  }
}

.head .menu > div {
  cursor: pointer;
}

.head .menu > div:hover span {
  color: #e2ac4f;
}

.head .menu > div span {
  font-size: 24px;
  transition: .5s;
  color: #c1964d;
}

.head .menu > div:not(:last-child) span {
  margin-left: 25px;
}

.head .menu > div.user {
  position: relative;
  cursor: pointer;
}

.head .menu > div.user:hover .drop {
  visibility: visible;
  opacity: 1;
  transform: translateY(5px);
}

.head .menu > div.user .drop {
  position: absolute;
  top: 100%;
  right: -112%;
  min-width: 135px;
  min-height: 75px;
  border-radius: 7px;
  transform: translateY(10px);
  background-color: white;
  text-align: center;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  z-index: 9;
  transition: .5s;
}

.head .menu > div.user .drop:after {
  content: "";
  width: 0;
  height: 0;
  display: block;
  border-width: 14px;
  border-color: transparent transparent #ffffff;
  border-style: solid;
  top: -19px;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
}

.head .menu > div.user .drop ul {
  padding: 10px 13px;
}

.head .menu > div.user .drop ul li {
  padding: 0 12px;
}

.head .menu > div.user .drop ul li a {
  color: #5b5b5b;
  transition: .5s;
  font-size: 14px;
  line-height: 2;
}

.head .menu > div.user .drop ul li a:hover {
  color: #c1964d;
}

.head .menu > div.user .drop ul li:not(:last-child) {
  border-bottom: 1px solid #f2f2f2;
}

.head .menu > div.user .drop ul li a {
  transition: .5s;
}

.head .menu > div.cart {
  position: relative;
}

@media (min-width: 992px) {
  .head .menu > div.cart span {
    margin-left: 0;
  }
}

.head .menu > div.searBtn {
  display: none;
}

@media (max-width: 991px) {
  .head .menu > div.searBtn {
    display: block;
  }
}

