  border-bottom: 0;
}

.owl-nav button.owl-next span {
  border-right: 0;
  border-top: 0;
}

.owl-nav button span {
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #c1964d;
  transform: rotate(45deg);
}

.owl-dots {
  position: absolute;
  bottom: 40px;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

@media (max-width: 767px) {
