.product {
  position: relative;
  text-align: center;
  background-color: transparent;
  overflow: hidden;
  transition: .5s;
}

.product:hover .details {
  background-color: #FFF;
}

.product:hover .pic img {
  transform: scale(1.03);
}

.product .pic {
  height: 290px;
  position: relative;
  border: 1px solid #f2f2f2;
  overflow: hidden;
  align-items: flex-end;
}

.product .pic:hover:after {
  animation: shape .5s linear;
}

.product .pic:after {
  content: "";
  position: absolute;
  top: -103%;
  left: -100%;
  width: 100%;
  height: 170%;
  transition: .7s ease;
  background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.3));
  transform: rotate(45deg);
}

.product .pic .top {
  width: 36px;
  height: 30px;
  background-color: #c1964d;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 9;
  color: #FFF;
  font-size: 12px;
  font-family: 'frtBold';
}

.product .detail {
  padding: 15px 0 0px 0;
  background-color: transparent;
}

.product > a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.product > img {
  width: initial;
  height: 167px;
  display: block;
  margin: auto;
}

.product .pro-title {
  font-size: 14px;
  font-family: "frtBold";
  color: #5b5b5b;
  transition: .5s;
}

.product .pro-title:hover {
  color: #c1964d;
}

.product span {
  font-size: 16px;
  font-family: "frtBold";
  display: inline-block;
  direction: ltr;
}

.product span.price {
  color: #c1964d;
  margin-right: 8px;
}

.product span.discount {
  color: #b0b0b0;
  text-decoration: line-through;
  font-family: "frt";
}

.boxes {
  padding: 55px 0;
}

@media (max-width: 991px) {
  .boxes .col-md-4:not(:last-child) .box {
    margin-bottom: 25px;
  }
}

.boxes .box {
  padding: 0 15px;
  flex-direction: column;
  text-align: center;
}

.boxes .box .pic {
  height: 50px;
  margin-bottom: 17px;
}

.boxes .box img {
  transition: .5s;
}

.boxes .box p {
  font-size: 16px;
  color: #5b5b5b;
}

@media (max-width: 991px) {
  .boxes .box {
    padding: 0 25px;
  }
}

footer {
  padding: 58px 0;
  padding-bottom: 0;
  background-color: #B38841;
}

@media (max-width: 991px) {
  footer {
    padding: 40px 0 20px;
  }
}

@media (min-width: 1200px) {
  footer .foot-links {
    text-align: right;
  }
}

@media (max-width: 991px) {
  footer .foot-links {
    margin-bottom: 15px;
  }
}

footer .foot-links .collapse {
  display: block;
}

@media (max-width: 991px) {
  footer .foot-links .collapse {
    display: none;
  }
}

footer .foot-links .collapse.in {
  display: block;
}

footer .foot-links > button {
  font-size: 16px;
  font-family: "frtBold";
  color: #fff;
  margin-bottom: 15px;
  background-color: transparent;
  border: 0;
  padding: 0;
  pointer-events: none;
}

footer .foot-links > button.trans:after {
  content: "\2212";
}

@media (max-width: 991px) {
  footer .foot-links > button {
    width: 100%;
    margin: auto;
    pointer-events: all;
    margin-bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media (max-width: 480px) {
  footer .foot-links > button {
    width: 100%;
  }
}

footer .foot-links > button:after {
  content: "\002B";
  margin-left: 5px;
  font-size: 18px;
  float: left;
  display: none;
}

@media (max-width: 991px) {
  footer .foot-links > button:after {
    display: block;
  }
}

@media (max-width: 991px) {
  footer .foot-links ul {
    padding-top: 10px;
    padding-right: 10px;
  }
}

footer .foot-links ul li:not(:last-child) {
  margin-bottom: 10px;
}

footer .foot-links a {
  font-size: 14px;
  font-family: "frt";
  backface-visibility: hidden;
  color: #f9d392;
  transition: .5s;
}

footer .foot-links a:hover {
  color: #FFF;
}

@media (min-width: 992px) {
  footer .newsletter {
    margin-top: 35px;
    text-align: center;
  }
}

@media (min-width: 1200px) {
  footer .newsletter {
    margin-top: 0;
    text-align: right;
  }
}

@media (max-width: 991px) {
  footer .newsletter {
    margin-top: 35px;
    text-align: center;
  }
}

footer .newsletter > p {
  font-size: 16px;
  font-family: "frtBold";
  color: #fff;
  margin-bottom: 20px;
}

footer .newsletter > span {
  font-size: 14px;
  font-family: "frt";
  color: #fff;
  margin-bottom: 15px;
  display: block;
}

footer .frm {
  position: relative;
  margin-bottom: 17px;
  width: 360px;
}

@media (min-width: 992px) {
  footer .frm {
    margin: auto;
    margin-bottom: 17px;
  }
}

@media (max-width: 991px) {
  footer .frm {
    margin: auto;
    margin-bottom: 17px;
  }
}

@media (max-width: 375px) {
  footer .frm {
    width: 280px;
  }
}

footer .frm input {
  padding: 0 15px;
  height: 40px;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #d8af68;
  color: #d7ad67;
  font-size: 14px;
  background-color: transparent;
  transition: .5s;
}

footer .frm input::placeholder {
  color: #d7ad67;
}

footer .frm input:focus {
  border-bottom: 1px solid #FFF;
}

footer .frm button {
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  width: 37px;
  height: 30px;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

footer .frm button:hover span {
  color: #f9d392;
}

footer .frm button span {
  transition: .5s;
  font-size: 20px;
  color: #fff;
}

footer .pay {
  margin-top: 35px;
  margin-bottom: 50px;
  display: block;
}

@media (max-width: 991px) {
  footer .pay {
    margin-top: 0px;
    margin-bottom: 0px;
  }
}

footer .pay img {
  margin: auto;
}

.end {
  background-color: #B38841;
}

.end .container {
  border-top: 1px solid #BB9454;
  padding: 0;
}

.end .content {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 767px) {
  .end .content {
    text-align: center;
    padding: 10px;
  }
}

@media (max-width: 600px) {
  .end .content {
    flex-direction: column-reverse;
    padding: 10px 0 5px;
  }
}

.end .content p, .end .content a {
  font-size: 14px;
  color: #ffe9c3;
  margin-bottom: 0;
}

@media (max-width: 767px) {
  .end .content p, .end .content a {
    font-size: 13px;
  }
}

@media (max-width: 600px) {
  .end .content p, .end .content a {
    margin-bottom: 10px;
  }
}

@media (max-width: 600px) {
  .end .content p {
    margin-top: 10px;
  }
}

@media (max-width: 480px) {
  .end .content p {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 0;
  }
}

.end .content a img {
  margin-right: 5px;
}

@media (max-width: 480px) {
  .end .content a {
    margin-top: 10px;
  }
}

.end .content .social a {
  font-size: 18px;
  transition: .5s;
  color: #e9c17e;
}

.end .content .social a:not(:last-child) {
  margin-left: 25px;
}

.end .content .social a:hover {
  color: #FFF;
}
