@import 'main';
// End Of Import
// Preloader

.preloader{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-white;
    @extend .flix;
    flex-direction: column;
    z-index: 9999;
}

// Preloader

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba($color-prim, 0.8);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 99;
    display: none;

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.95);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -261px;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    &.open{
        right: 0;
    }

    .options {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #888;
        }

        .close1 {
            display: flex;
            font-size: 20px;
            color: #dedede;
            cursor: pointer;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 260px;
        // overflow: auto;
        padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid #dedede;
            }

            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: $color-prim;
                transition: .5s;

                &:hover{
                    color: $color-sec;
                }
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

// Head

.head{

    background-image: url("../images/logoBack.png");
    background-position: center;
    background-repeat: no-repeat;
    background-position-y: top;

    .container{
        position: relative;
    }

    .up{
        position: absolute;
        top: 0;
        left: 0;
        width: max-content;
        @extend .flix;
        padding-top: 15px;
        padding-left: 15px;


        .lang{
            font-size: 12px;
            color: #b0b0b0;
            margin-right: 20px;
            transition: .5s;

            &:hover{
                color: $color-prim;
            }
        }

        .social{

            a{
                color: #c3c3c3;
                font-size: 16px;
                margin-left: 22px;
                transition: .5s;

                &:hover{
                    color: $color-prim;
                }

                &:last-child{
                    margin-left: 10px;
                }
            }
        }
    
        @include Sm{
            display: none;
        }
    }

    .content{
        padding: 20px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Sm{
            flex-direction: column;
        }
    }

    .search{
        position: relative;
        width: max-content;
        margin-top: 25px;
        width: max-content;

        &.open{
            opacity: 1;
            visibility: visible;
            top: 60%;

            .bar{
                cursor: pointer;

                span{
                    opacity: 1;
                }
            }
        }

        @include Sm{
            order: 3;
            position: absolute;
            top: 100%;
            z-index: 999;
            opacity: 0;
            visibility: hidden;
            display: flex;
            justify-content: center;
        }

        @include Xs{
            margin-top: 10px;
        }

        // @include mob480{
        //     width: 100%;
        // }

        .bar{
            display: none;
            width: 40px;
            height: 40px;
            position: relative;
            
            span{
                display: block;
                width: 20px;
                height: 2px;
                background-color: #b0b0b0;
                position: absolute;
                transition: .3s;
                
                &:first-child{
                    transform-origin: left;
                    transform: rotate(47deg);
                    top: 25%;
                    left: 16.5px;
                }

                &:last-child{
                    transform-origin: right;
                    transform: rotate(-47deg);
                    top: 25%;
                    right: 10px;
                }
            }

            @include Sm{
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: -85px;
            }
        }

        input{
            width: 260px;
            height: 55px;
            border: 0;
            border-bottom: 1px solid #e2e2e2;
            padding: 0 10px;
            color: #b0b0b0;
            transition: .5s;

            &::placeholder{
                color: #b0b0b0;
            }

            &:focus{
                border-bottom: 1px solid $color-prim;
            }

            @include Sm{
                width: 600px;
                background-color: transparent;
                border-bottom: 1px solid #e2e2e2;
                color: #b0b0b0;

                &::placeholder{
                    color: #b0b0b0;
                }
                
                &:focus{
                    border-bottom: 1px solid $color-sec;
                }
            }

            @include Xs{
                width: 400px;
            }

            @include mob480{
                width: 300px;
            }
        }

        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 30px;
            border-radius: 50%;
            background-color: transparent;
            border: 0;
            font-size: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;

            @include Md{
                left: 10px;
            }

            @include Lg{
                left: 10px;
            }

            @include Sm{
                left: 10px;
            }
                    
                span{
                    transition: .5s;
                    color: $color-prim;
                }

            &:hover{
                span{
                    color: #c3c3c3;
                }
            }
        }
    }

    .overlay{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color-white, 0.95);
        z-index: 9;
        display: none;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        z-index: 99;
    }

    .menu{
        margin-top: 25px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 260px;

        @include Sm{
            justify-content: center;
        }

        & > div{
            cursor: pointer;

            &:hover{
                span{
                    color: #e2ac4f;
                }
            }

            span{
                font-size: 24px;
                transition: .5s;
                color: $color-prim;
            }

            &:not(:last-child){

                span{
                    margin-left: 25px;
                }
            }

            &.user{
                position: relative;
                cursor: pointer;

                &:hover{
                    
                    .drop{
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(5px);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    right: -112%;
                    min-width: 135px;
                    min-height: 75px;
                    border-radius: 7px;
                    transform: translateY(10px);
                    background-color: rgb(255, 255, 255);
                    text-align: center;                    
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    opacity: 0;
                    visibility: hidden;
                    z-index: 9;
                    transition: .5s;

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }

                    ul{
                        padding: 10px 13px;

                        li{
                            padding: 0 12px;

                            a{
                                color: $color-sec;
                                transition: .5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-prim;
                                }
                            }

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                transition: .5s;
                            }
                        }
                    }
                }
            }

            &.cart{
                position: relative;

                @include Md{

                    span{
                        margin-left: 0;
                    }
                }
            }

            &.searBtn{
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }
    }
}

// Head

// Navbar

.navBar{
    background-color: $color-prim;
    height: 50px;
    @extend .flix;

    @include Sm{
        display: none;
    }

    & > ul{
        @extend .flix;

        & > li{
            position: relative;

            i{
                font-size: 8px;
                margin-right: 5px;
            }

            &:not(:last-child){
                margin-left: 30px;
                
                @include Md{
                    margin-left: 20px;
                }
                
                @include Lg{
                    margin-left: 30px;
                }
            }

            a{
                font-size: 17px;
                font-family: "frtBold";
                line-height: 2.752;
                color: $color-white;
                transition: .5s;
                
                &:hover{
                    color: #ffd792;
                }

                @include Md{
                    font-size: 15px;
                }

                @include Lg{
                    font-size: 17px;
                }
            }
        }
    }
}

// Navbar

.owl-nav{
    position: absolute;
    top: 49%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm{
        display: none;
    }

    button{

        &.owl-prev{
            span{
                border-left: 0;
                border-bottom: 0;
            }
        }

        &.owl-next{
            span{
                border-right: 0;
                border-top: 0;
            }
        }

        span{
            display: block;
            width: 15px;
            height: 15px;
            border: 1px solid $color-prim;
            transform: rotate(45deg);
        }
    }
}

// Slider Styles

.owl-dots{
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs{
        bottom: 15px;
    }
    
    button{

        &:not(:last-child){
            margin-left: 5px;
        }

        &.active{

            span{
                background-color: $color-prim;
            }
        }

        span{
            display: block;
            width: 10px;
            height: 10px;
            background-color: rgba($color-sec, 0.4);
        }
    }
}

// Main Slider

.main-slider{
    overflow: hidden;

    &:hover{

        .owl-nav{
            width: 100%;
        }
    }


    .owl-nav{
        position: absolute;
        top: 49%;
        transform: translate(50%,-50%);
        right: 50%;
        width: 110%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: .5s;

        @include Sm{
            display: none;
        }

        button{
            width: 40px;
            height: 40px;
            background-color: rgba($color-black, 0.6) !important;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .5s;

            &:hover{
                background-color: $color-prim !important;
            }

            &.owl-prev{
                span{
                    border-left: 0;
                    border-bottom: 0;
                    transform: rotate(45deg) translate(-3px, 3px);
                }
            }

            &.owl-next{
                transform: translateX(-1px);
                span{
                    border-right: 0;
                    border-top: 0;
                    transform: rotate(45deg) translate(3px, -3px);
                }
            }

            span{
                display: block;
                width: 15px;
                height: 15px;
                border: 1px solid #fff;
                transform: rotate(45deg);
            }
        }
    }

    .owl-dots{
        position: absolute;
        bottom: 40px;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        @include mob600{
            bottom: 10px;
        }
        
        button{

            &:not(:last-child){
                margin-left: 5px;
            }

            &.active{

                span{
                    background-color: $color-prim;
                }
            }

            span{
                display: block;
                width: 10px;
                height: 10px;
                background-color: rgba($color-black, 0.6);
            }
        }
    }
}

// Main Slider

// Categories

.categories{
    padding: 33px 0 15px;

    @include Sm{
        padding: 33px 0 33px;
    }

    .col-md-3{

        @include Sm{
            padding: 0;
        }
    }

    .owl-dots{
        bottom: 0;
    }

    .category{
        text-align: center;
        margin-bottom: 30px;

        &:hover{
            .pic{

                img{
                    transform: scale(1.04) translateZ(0);
                }

                .title{
                    background-color: $color-prim
                }
            }
        }

        .pic{
            position: relative;
            overflow: hidden;

            &:after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba($color-black, .05);
            }

            img{
                display: block;
                margin: auto;
                transition: .5s;
                backface-visibility: hidden;
                transform: translateZ(0);
            }
        }

        .title{
            height: 53px;
            width: 100%;
            background-color: rgba($color-prim, 0.85);
            font-size: 17px;
            font-family: "frtBold";
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            bottom: 0;
            left: 0;
            transition: .5s;
            
            p{
                color: #fff;
                margin: 0;
            }
        }
    }
}

// Categories


// Products

.disc.custom{

    padding: 60px 0;
    background-color: #f6f6f6;

    @include Xs{
        padding: 30px 0 60px;
    }

    .disc-head{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 25px;
    
        & > p{
            font-size: 20px;
            font-family: "frtBold";
            color: $color-sec;
            margin-bottom: 0;

            @include Sm{
                margin-right: 0;
            }

            @include mob375{
                font-size: 20px;
            }
        }
    
        & > a{
            color: #a5a5a5;
            transition: .5s;
            font-size: 15px;
            color: rgb(207, 207, 207);

            &:hover{
                color: $color-sec;
            }
        }
    }
    
    .owl-carousel{

        .owl-item{
            display: flex;
            justify-content: center;
        }

        .owl-nav{
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 105%;
            height: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 30px;
            margin-top: 0;
            color: #c4c4c4;
            padding: 0;
            right: unset;

            @include Md{
                width: 100%;
            }

            @include Lg{
                width: 105%;
                top: 40%;
            }

            @include Sm{
                display: none;
            }

            button{
                width: 15px;
                height: 15px;
                border: 2px solid #d0d0d0;
                border-right: none;
                border-bottom: none;
                transform: rotate(-45deg);
                border-radius: 1px;
                transition: .5s;

                &.owl-prev{
                    transform: rotate(135deg);
                }

                &:hover{
                    background-color: transparent;
                    border: 2px solid #0d0d0d;
                    border-right: none;
                    border-bottom: none;
                }

                span{
                    display: none;
                }
            }
        }

        .owl-dots{
            display: none;

            @include Sm{
                display: flex;
                justify-content: center;
                align-items: center;
                position: unset;
                transform: translateY(35px);
                margin-bottom: 15px;
            }
        }

        .owl-item{

            img{
                width: initial;
                margin: auto;
                transition: .5s;
            }
        }
    }
}

.product{
    position: relative;
    text-align: center;
    background-color: transparent;
    overflow: hidden;
    transition: .5s;

    &:hover{

        .details{
            background-color: $color-white;
        }

        .pic{
            img{
                transform: scale(1.03);
            }
        }
    }

    .pic{
        height: 290px;
        position: relative;
        border: 1px solid #f2f2f2;
        overflow: hidden;
        @extend .flix;
        align-items: flex-end;

        &:hover{
            &:after{
                animation: shape .5s linear;
            }
        }

        &:after{
            content: "";
            position: absolute;
            top: -103%;
            left: -100%;
            width: 100%;
            height: 170%;
            transition: .7s ease;
            background-image: linear-gradient(to right, transparent, rgba($color-white, 0.3));
            transform: rotate(45deg);
        }

        .top{
            width: 36px;
            height: 30px;
            background-color: $color-prim;
            position: absolute;
            top: 10px;
            right: 10px;
            z-index: 9;
            color: $color-white;
            @extend .flix;
            font-size: 12px;
            font-family: 'frtBold';

            // @include Md{
            //     top: 66px;
            // }

            // @include Lg{
            //     top: 10px;
            // }
        }
    }

    .detail{
        padding: 15px 0 0px 0;
        background-color: transparent;
    }

    & > a{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    & > img{
        width: initial;
        height: 167px;
        display: block;
        margin: auto;
    }

    .pro-title{
        font-size: 14px;
        font-family: "frtBold";
        color: $color-sec;
        // margin-top: 0px;
        transition: .5s;

        &:hover{
            color: $color-prim
        }
    }

    span{
        font-size: 16px;
        font-family: "frtBold";
        display: inline-block;
        direction: ltr;

        &.price{
            color: $color-prim;
            margin-right: 8px;
        }

        &.discount{
            color: #b0b0b0;
            text-decoration: line-through;
            font-family: "frt";
        }
    }
}

//Products

// Boxes

.boxes{
    padding: 55px 0;

    .col-md-4{
        &:not(:last-child){
            .box{
                @include Sm{
                    margin-bottom: 25px;
                }
            }
        }
    }

    .box{
        padding: 0 15px;
        @extend .flix;
        flex-direction: column;
        text-align: center;

        .pic{
            @extend .flix;
            height: 50px;
            margin-bottom: 17px;
        }

        img{
            transition: .5s;
        }

        p{
            font-size: 16px;
            color: $color-sec;
        }

        @include Sm{
            padding: 0 25px;
        }
    }
}

// Boxes

// footer

footer{
    padding: 58px 0;
    padding-bottom: 0;
    background-color: #B38841;

    @include Sm{
        padding: 40px 0 20px;
    }

    .foot-links{

        @include Lg{
            text-align: right;
        }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 15px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 14px;
            font-family: "frt";
            backface-visibility: hidden;
            color: #f9d392;
            transition: .5s;

            &:hover{
                color: $color-white;
            }
        }
    }

    .newsletter{
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0;
            text-align: right;
        }

        @include Sm{
            margin-top: 35px;
            text-align: center;
        }

        & > p{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 20px;
        }

        & > span{
            font-size: 14px;
            font-family: "frt";
            color: #fff;
            margin-bottom: 15px;
            display: block;
        }
    }

    .frm{
        position: relative;
        margin-bottom: 17px;
        width: 360px;

        @include Md{
            margin: auto;
            margin-bottom: 17px;
        }

        @include Sm{
            margin: auto;
            margin-bottom: 17px;
        }

        @include mob375{
            width: 280px;
        }

        input{
            padding: 0 15px;
            height: 40px;
            width: 100%;
            border: 0;
            border-bottom: 1px solid #d8af68;
            color: #d7ad67;
            font-size: 14px;
            background-color: transparent;
            transition: .5s;

            &::placeholder{
                color: #d7ad67;
            }

            &:focus{
                border-bottom: 1px solid $color-white;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 5px;
            transform: translateY(-50%);
            width: 37px;
            height: 30px;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: transparent;

            &:hover{

                span{
                    color: #f9d392;
                }
            }

                
            span{
                transition: .5s;
                font-size: 20px;
                color: #fff;
            }
        }
    }

    .pay{
        margin-top: 35px;
        margin-bottom: 50px;
        display: block;

        @include Sm{
            margin-top: 0px;
            margin-bottom: 0px;
        }

        img{
            margin: auto;
        }
    }
}

// End Of Footer

// End

