    background-color: #B38841;

    .container{
        border-top: 1px solid #BB9454;
        padding: 0;
    }

    .content{
        padding: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        
        @include Xs{
            text-align: center;
            padding: 10px;
        }
        
        @include mob600{
            flex-direction: column-reverse;
            padding: 10px 0 5px;
        }

        p,a{
